import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Checkout from './canvas/Checkout';
import Layout from '../components/Layout';
import Dashboard from './canvas/Dashboard';
import ManagementProd from './canvas/Management_prod';
import AddProduct from './canvas/AddProduct';
import UpdateProduct from './canvas/UpdateProduct';
import ManagementCat from './canvas/Management_cat';

const Home = () => (
  <BrowserRouter>
        <Layout>
          <Switch>
            <Route exact path="/PointOfSale/spa" component={Dashboard} />
            <Route exact path="/PointOfSale/checkout" component={Checkout} />
            <Route exact path="/PointOfSale/products" component={ManagementProd} />
            <Route exact path="/PointOfSale/cat" component={ManagementCat} />
            <Route exact path="/PointOfSale/add" component={AddProduct} />
            <Route exact path="/PointOfSale/edit" component={UpdateProduct} />
          </Switch>
        </Layout>
  </BrowserRouter>

);

export default Home;